.landing-header-container {
  display: flex;
  flex-wrap: wrap;
  height: calc(100vh - 84px - 52px);
  max-height: 900px;
  width: 100vw;

  @media screen and (max-width: 991px) {
    justify-content: center;
    height: calc(100%);
  }
}

.landing-info-page {
  .subscribe-container {
    max-width: 1016px;
    @media screen and (max-width: 1046px) {
      margin-left: 15px;
      margin-right: 15px;
    }
  }
}
